 que permitan sustituir la tracción animal y generaban más eficiencia y permitían el comercio entre lugares lejanos. Algunos inventos correspondientes a esta época son: los relojes, molinos de viento, la brújula, entre otros.")]),_c('p',[_vm._v("En esta edad es representativo la invención de la imprenta, con la cual mejoró notablemente la comunicación entre las diferentes poblaciones.")])])]),_c('div',{staticClass:"row align-items-center p-4",attrs:{"titulo":"Edad Moderna"}},[_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{staticClass:"mb-4",attrs:{"src":require("@/assets/curso/temas/sliders/LT_04.png"),"alt":"Hombres primitivos con un mamut"}})])]),_c('div',{staticClass:"col-lg-7"},[_c('h4',[_vm._v("La tecnología en la Edad Moderna")]),_c('p',[_vm._v("Esta época se caracteriza por la invención de artefactos como el telescopio, microscopio, reloj de péndulo, lápiz, calculadora, entre otros.")]),_c('h5',[_vm._v("Revoluciones industriales")]),_c('p',[_vm._v("El conocimiento de la física y la ciencia en general permitió que los avances siguieran siendo representativos a tal punto que se han vivido cuatro revoluciones industriales que influyeron no solo en la productividad, sino en el tipo de empleos y de emprendimientos que surgieron.")])])]),_c('div',{staticClass:"row align-items-center p-4",attrs:{"titulo":"Edad Contemporánea"}},[_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{staticClass:"mb-4",attrs:{"src":require("@/assets/curso/temas/sliders/LT_05.png"),"alt":"Hombres primitivos con un mamut"}})])]),_c('div',{staticClass:"col-lg-7"},[_c('h4',[_vm._v("La tecnología en la Edad Contemporánea")]),_c('h5',[_vm._v("Transición de lo análogo a lo digital")]),_c('p',[_vm._v("Ahora pensemos por un momento en lo que sucedía en los años 90: si una persona quería entregar un trabajo escrito, debía hacerlo a mano o utilizar una máquina de escribir, esta última generó un impacto positivo para quien tenía dificultades al leer diversas caligrafías; pero si en 